body{

background-color: rgb(255, 255, 255);;

display: grid;
height: 250vh;

gap: 5%;

grid-template-rows: 10% 75% 15%;




}

.list{
    display: flex;
    color: var(--black) ;
    list-style-type: none;
    gap: 20px;

    font-size: 1rem;
    font-family: var(--font2);
    
    justify-self: center;
    justify-content: center;
    
    font-weight: 600;
}

.list li{
    cursor: pointer;
    
    
}  

.list li:hover {
    
    text-decoration:underline ;
    text-underline-offset: 10px;
    
}
#container{

   display: flex;
