}


.container-logo{
    color: var(--color-dark);
    font-size: 5rem;
    font-family: var(--font1) ;
    letter-spacing: 20px;
   font-weight: 500;
   text-align: center;
}

.container-text{
    font-size: 1.2rem;
    color: var(--color-gray-dark);
    text-align: center;
    width: 40%;
    min-width: 300px;
    line-height:30px;
   
  
    font-family: var(--font2);
}

.container-btn , .submit{
    background-color: var(--color-dark);
    color: var(--color-light);
    border: 1px solid var(--color-dark);
    font-size: 1rem;
    font-family: var(--font2);
    padding: 20px 40px;
    border-radius: 5px ;
    cursor: pointer;
    
}
.container-btn:hover ,.submit:hover{
    background-color: var(--color-gray);
    border-color: var(--color-gray);
}

#logo{
    cursor: pointer;
}
#logo , .footer-logo{ 
