    align-items: center;
    justify-content: space-around;
    
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2); 
    font-family: var(--font2);
    color: var(--color-gray-dark);
    font-size: 1rem;
    font-weight: 500;
}
